<template>
  <div>
    <AppBase :options="options" 
             @update-filter="updateFilter">
    </AppBase>
    <PicGallery
      :piclist="piclist"
      style="margin-top: 50px;"
    ></PicGallery>
  </div>
</template>

<script>
import PicGallery from "./outer-product/PicGallery";
import AppBase from "./outer-product/AppBase";
import axios from 'axios'
export default {
  name: "Main",
  components: {
    PicGallery,
    AppBase
  },
  data() {
    return {
      piclist: [],
      data: [],
      options: [],
      filterparams: [],
      marginleft: 0,
      currentPage: 1,
      searchtag: "",
      showetagditor: false,
      showeditor: false,
      acecontent: "",
      currentPicTag: {}
    };
  },
  computed: {
    getheight() {
      return Math.max(...this.yindex) + "px";
    }
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
    this.updateLoadMore();
    axios
      .get('http://localhost:8081/static/json/data.json')
      .then(response => {
        this.data = response.data.data.items
        this.options = Array.from(new Set(this.data.map(item => item["topic"]).reduce(function(prev, next) {
            return prev.concat(next);
        }))).map(item => {return {name: item, code: item}});
        this.updateLoadMore()
      })
  },
  methods: {
    updateFilter(params){
      this.piclist = []
      this.filterparams = params.map(item => item.name)
      this.updateLoadMore()
    },
    resetConfig() {
      this.currentIndex = 0;
      this.currentPage = 1;
      this.yindex = [];
      this.piclist = [];
      for (let i = 0; i < this.sizeinline; i++) {
        this.yindex.push(0);
      }
    },

    refreshACEeditor(data, config) {
      this.showeditor = true;
      document.getElementsByTagName("html")[0].style.overflow = "hidden";
      this.acecontent = data;
      this.currentPicTag = config;
    },
    updateLoadMore() {
      let paramset = new Set(this.filterparams)
      let localdata = []
      if(this.filterparams.length == 0){
        localdata = this.data
      }else{
        localdata = this.data.filter(item => {
          return item["topic"].filter(im => paramset.has(im)).length >0 ? true: false
        })
      }
      if(this.piclist.length < localdata.length){
        this.piclist = localdata.slice(0, (this.piclist.length+20 > localdata.length)?localdata.length: (this.piclist.length+20))
      }
      this.stopfetch = false;
    },
    onScroll(e) {
      if (
        !this.stopfetch &&
        Math.abs(document.getElementsByTagName("html")[0].scrollHeight - (window.scrollY + window.innerHeight)) < 2
      ) {
        this.currentPage += 1;
        this.stopfetch = true;
        this.updateLoadMore();
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.stopscroll {
  overflow: hidden;
}
.startscroll {
  overflow: auto;
}
.texEdi {
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
}
</style>



