class Service {
    constructor() {
        this.baseurl = "http://127.0.0.1:5333/"
        this.currentPage = 1
        return this
    }
    getAllTags(callback, config) {
        let that = this
        let xhr = new XMLHttpRequest();
        xhr.open('GET', DataService.baseurl + "gettaglist");
        xhr.onreadystatechange = function() {
            if (xhr.readyState == 4) {
                let x = xhr.responseText.replace(/\'/g, "\"")
                let data = JSON.parse(x);
                DataService.tagBase = data.data;
                if (callback) {
                    that[callback]()
                }
            }
        }
        xhr.send();
    }
    getImages(callback, config) {
        let that = this
        let xhr = new XMLHttpRequest();
        xhr.open('GET', DataService.baseurl + "getimgs?page=" + config.page + "&tag=" + config.tag);
        xhr.onreadystatechange = function() {
            if (xhr.readyState == 4) {
                let x = xhr.responseText.replace(/\'/g, "\"")
                let data = JSON.parse(x);
                if (callback) {
                    that[callback](data.data)
                }
            }
        }
        xhr.send();
    }
    getOneTexFile(callback, config) {
        let that = this
        let xhr = new XMLHttpRequest();
        xhr.open('GET', 'static/tex/' + config.name.replace(".png", "") + ".tex");
        xhr.onreadystatechange = function() {
            if (xhr.readyState == 4) {
                if (callback) {
                    that[callback](xhr.responseText, config)
                }
            }
        }
        xhr.send();
    }
    deleteTags(callback, config) {
        let that = this
        let xhr = new XMLHttpRequest();
        xhr.open('POST', DataService.baseurl + "deletetags");
        xhr.onreadystatechange = function() {
            if (xhr.readyState == 4) {
                if (callback) {
                    that[callback](xhr.responseText, config)
                }
            }
        }
        xhr.send(JSON.stringify(config));
    }
    addTags(callback, config) {
        let that = this
        let xhr = new XMLHttpRequest();
        xhr.open('POST', DataService.baseurl + "addtags");
        xhr.onreadystatechange = function() {
            if (xhr.readyState == 4) {
                if (callback) {
                    that[callback](xhr.responseText, config)
                }
            }
        }
        xhr.send(JSON.stringify(config));
    }
    queryImage(callback, config) {
        let that = this
        let xhr = new XMLHttpRequest();
        xhr.open('GET', DataService.baseurl + "queryImage?userid=" + config.userid);
        xhr.onreadystatechange = function() {
            if (xhr.readyState == 4) {
                if (callback) {
                    that[callback](JSON.parse(xhr.responseText.replace(/\'/g, "\"")), config)
                }
            }
        }
        xhr.send();
    }
    asureImages(callback, config) {
        let that = this
        let xhr = new XMLHttpRequest();
        xhr.open('POST', DataService.baseurl + "asureImages?userid=" + config.userid);
        xhr.onreadystatechange = function() {
            if (xhr.readyState == 4) {
                if (callback) {
                    that[callback](JSON.parse(xhr.responseText.replace(/\'/g, "\"")), config)
                }
            }
        }
        xhr.send(JSON.stringify(config));
    }

}

const DataService = new Service()
export default DataService


